import './App.css';
import {  HashRouter, Route } from 'react-router-dom';

import Navi  from "./Components/Navi.js";
import Pricing from "./Components/Pricing.js";
function App() {
  return (
    <div className="App">
      <HashRouter basename="/">

        <Navi />
        
          <Route path='/' exact component={Pricing} />
          <Route path='/pricing'  component={Pricing} />
          
        
      </HashRouter>

    </div>
  );
}




export default App;
